"use client"

import { useEffect, useState } from "react"
import Link from "next/link"
import { Menu, X, Search, User, ShoppingCart } from "lucide-react"

export default function Header() {
  const [isSticky, setIsSticky] = useState(false)
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false)

  // BRAND COLORS
  const PRIMARY = "#53675C"
  const SECONDARY = "#F8C8DC"

  const topMenuItems = [
    "Payments",
    "Delivery",
    "Terms and Conditions",
    "Working Hours",
  ]

  const navItems = [
    { label: "Home", href: "#" },
    { label: "About", href: "#about" },
    { label: "Blog", href: "#blog" },
    { label: "Store", href: "#store" },
    { label: "Contacts", href: "#contacts" },
  ]

  useEffect(() => {
    const handleScroll = () => setIsSticky(window.scrollY > 50)
    window.addEventListener("scroll", handleScroll)
    return () => window.removeEventListener("scroll", handleScroll)
  }, [])

  return (
    <>

      {/* ---------------------- TOP BAR (Solid Green) ---------------------- */}
      <div
        className="hidden md:block w-full border-b border-white/10"
        style={{
          backgroundColor: PRIMARY,
          position: "relative",
          zIndex: 50,
        }}
      >
        <div className="max-w-7xl mx-auto px-6">
          <div className="flex justify-between items-center h-12">

            {/* Left Links */}
            <div className="flex items-center gap-10">
              {topMenuItems.map((item) => (
                <a
                  key={item}
                  href="#"
                  className="text-white text-sm uppercase tracking-wide hover:text-[#F8C8DC]"
                >
                  {item}
                </a>
              ))}
            </div>

            {/* Right Icons */}
            <div className="flex items-center gap-8">
              <button className="flex items-center gap-2 text-white hover:text-[#F8C8DC]">
                <Search size={16} /> <span className="text-sm">search</span>
              </button>

              <a className="flex items-center gap-2 text-white hover:text-[#F8C8DC]">
                <User size={16} /> <span className="text-sm">login</span>
              </a>

              <button className="flex items-center gap-2 text-white hover:text-[#F8C8DC]">
                <ShoppingCart size={16} />
                <span className="text-sm">cart</span>
                <span className="w-6 h-6 flex items-center justify-center rounded-full bg-white text-black text-xs">
                  0
                </span>
              </button>
            </div>

          </div>
        </div>
      </div>

      {/* ---------------------- MAIN NAVBAR (Transparent) ---------------------- */}
      <nav
        className={`w-full transition-all duration-300 ${
          isSticky
            ? "fixed top-0 left-0 z-40 bg-transparent backdrop-blur-md"
            : "relative z-40 bg-transparent"
        }`}
        style={{
          marginTop: isSticky ? "0" : "0px", // do not push down content
        }}
      >
        <div className="max-w-7xl mx-auto px-6">
          <div className="flex justify-between items-center py-6">

            {/* LOGO */}
            <Link href="/" className="flex items-center gap-3">
              <div
                className="w-12 h-12 rounded-md flex items-center justify-center"
                style={{ backgroundColor: PRIMARY }}
              >
                <span className="text-white text-lg font-bold">A</span>
              </div>
              <span className="text-white tracking-widest text-xl uppercase">
                ANAPLAK
              </span>
            </Link>

            {/* Desktop Menu */}
            <div className="hidden lg:flex gap-10">
              {navItems.map((item) => (
                <Link
                  key={item.label}
                  href={item.href}
                  className="text-white hover:text-[#F8C8DC] uppercase tracking-[3px] text-sm"
                >
                  {item.label}
                </Link>
              ))}
            </div>

            {/* Right CTA */}
            <div className="hidden lg:flex items-center gap-8">
              <a
                href="tel:+919840088867"
                className="text-[#F8C8DC] text-lg tracking-wide"
              >
                +91-9840088867
              </a>

              <button
                className="px-10 py-3 text-white uppercase tracking-wide text-sm"
                style={{ backgroundColor: PRIMARY }}
              >
                An Appointment
              </button>
            </div>

            {/* Mobile Toggle */}
            <button
              onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
              className="lg:hidden text-white"
            >
              {isMobileMenuOpen ? <X size={28} /> : <Menu size={28} />}
            </button>

          </div>

          {/* ---------------------- Mobile Menu ---------------------- */}
          {isMobileMenuOpen && (
            <div className="lg:hidden pb-6 flex flex-col gap-6 mt-2 bg-black/40 backdrop-blur-md rounded-lg p-6">

              {navItems.map((item) => (
                <Link
                  key={item.label}
                  href={item.href}
                  className="text-white uppercase tracking-[3px] text-sm hover:text-[#F8C8DC]"
                >
                  {item.label}
                </Link>
              ))}

              <a href="tel:+919840088867" className="text-[#F8C8DC] text-lg">
                +91-9840088867
              </a>

              <button
                className="px-10 py-3 text-white uppercase tracking-wide text-sm rounded"
                style={{ backgroundColor: PRIMARY }}
              >
                An Appointment
              </button>

            </div>
          )}
        </div>
      </nav>
    </>
  )
}
