"use client"

import { useEffect, useRef, useState } from "react"

export default function About() {
  const sectionRef = useRef(null)
  const [visible, setVisible] = useState(false)

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => entry.isIntersecting && setVisible(true),
      { threshold: 0.2 }
    )
    if (sectionRef.current) observer.observe(sectionRef.current)
    return () => observer.disconnect()
  }, [])

  return (
    <section ref={sectionRef} className="bg-[#0E0E0E] py-20 md:py-28">
      <div className="max-w-7xl mx-auto px-4 grid grid-cols-1 md:grid-cols-2 gap-16">

        {/* LEFT */}
        <div
          className={`transition-all duration-[1200ms] ${
            visible ? "opacity-100 translate-y-0" : "opacity-0 translate-y-8"
          }`}
        >
          <img
            src="/about_img_01.jpg"
            className="w-full max-w-[811px] aspect-[811/1024] object-cover"
          />

          <h6 className="uppercase mt-6 text-white font-semibold text-[15px] leading-relaxed max-w-[380px]">
            Experience the artistry of hair styling with a team that values your comfort your time
            and your personal beauty journey
          </h6>
        </div>

        {/* RIGHT */}
        <div
          className={`transition-all duration-[1200ms] delay-200 ${
            visible ? "opacity-100 translate-y-0" : "opacity-0 translate-y-8"
          }`}
        >
          <h6 className="uppercase text-[#C8AFAE] tracking-[0.3em] text-[14px] mb-6">
            about hair salon
          </h6>

          {/* WORD REVEAL EFFECT */}
          <h2 className="text-white font-bold text-[42px] leading-[1.2] max-w-xl mb-6">
            Find the perfect balance between elegance and comfort with{" "}
            <span className="word-reveal">
              <span>hairstyles</span>
              <span>style</span>
              <span>fashion</span>
            </span>
          </h2>

          <p className="text-[#C2C2C2] text-[16px] leading-[1.75] max-w-xl mb-10">
            Our stylists use modern techniques and trusted products to create hairstyles that remain
            stylish and practical. We focus on both style and hair health ensuring beauty that lasts
            beyond the salon visit.
          </p>

          {/* LOWER IMAGE + ROTATING LOGO */}
          <div className="relative">
            <img
              src="/about_img_02.jpg"
              className="w-full max-w-[1000px] aspect-[1000/761] object-cover rounded-xl"
            />

            <img
              src="/logo.png"
              className="w-[170px] h-[170px] absolute -right-16 top-1/2 -translate-y-1/2 opacity-90 rotate-slow-delay"
            />
          </div>
        </div>

      </div>
    </section>
  )
}
